import s from "./ProfileContent.module.scss";
import style from "./../../common/formsControls.module.scss";
import React from "react";
import StatusProfileHook from "./StatusProfile/StatusProfileHook";
import {inputCreator} from "../../common/formInputCreator";
import { reduxForm } from "redux-form";
import {Input, TextArea} from "../../common/formsControls";

const ProfileDataForm = ({handleSubmit,error,...props})=>{
    return(
        <form onSubmit={handleSubmit}>
            <div><button>Save</button></div>
            {error && <div className={style.SistemErrorMessage}>{error} </div>}
            <div className={s.name}><b>Имя:</b>{inputCreator("fullName", [], Input, "Имя")}</div>

            <div><b>Ищу ли я работу?:</b>  {inputCreator("lookingForAJob", [], Input, "lookingForAJob",{ type: "checkbox" })}</div>


            <div><b>Мои знания?:</b>{inputCreator("lookingForAJobDescription", [], TextArea, "lookingForAJobDescription")}</div>
            <div><b>About me:</b>{inputCreator("aboutMe", [], TextArea, "aboutMe")}</div>

            <StatusProfileHook status={props.status} updateStatusThunk={props.updateStatusThunk}/>
             <div><b>contacts:</b></div>
            {Object.keys(props.profile.contacts).map(key => {
                    return <div><b>{key}: {inputCreator("contacts."+key, [], Input, key)}</b></div>
                }
            )}

        </form>
    )
}

let ProfileFormControl = reduxForm({ form: 'edit-profile' })(ProfileDataForm)
export default ProfileFormControl