import React,{useState} from "react";
import s from "./ProfileContent.module.scss";
import Preloader from "../../common/preloader/Preloader";
import StatusProfileHook from "./StatusProfile/StatusProfileHook";
import photoImgPerson from "../../../access/images/user.png";
import ProfileDataForm from "./ProfileDataForm";
import {goToEditProfile} from "../../../Redux/profile-reducer";

function ProfileContent(props) {



    if (!props.profile) {
        return <Preloader/>
    }

    let changePhoto = (e) => {
        if (e.target.files.length) {
            props.savePhoto(e.target.files[0]);
        }
    }

    const onSubmit = (formData) => {

       props.setEditProfile(formData);
    }

    return (
        <div className={s.content_person}>
            <div className={s.img_person}>
                <img src={props.profile.photos.large || photoImgPerson} alt=""/>
                {props.isUserUndef || <input onChange={changePhoto} type={"file"}/>}
            </div>
            {props.editProfile?
                <ProfileDataForm initialValues={props.profile} onSubmit={onSubmit} profile={props.profile} updateStatusThunk={props.updateStatusThunk} status={props.status}/>:
                <ProfileData isUserUndef={props.isUserUndef} goToEditProfile={props.goToEditProfile}  profile={props.profile} updateStatusThunk={props.updateStatusThunk} status={props.status}/>}
        </div>
    );
}

const ProfileData = (props)=>{
    return(
        <div className={s.info_person}>
            {!props.isUserUndef && <div onClick={()=>props.goToEditProfile(true)}> <button>edit</button></div>}
            <div className={s.name}><b>Имя:</b>{props.profile.fullName}</div>
            <div><b>Ищу ли я работу?:</b> {props.profile.lookingForAJob ? "Да" : "Нет"}</div>
            {props.profile.lookingForAJob &&
            <div><b>Мои скилы:</b>{props.profile.lookingForAJobDescription}</div>}
            <div><b>About me:</b>{props.profile.aboutMe}</div>

            <StatusProfileHook status={props.status} updateStatusThunk={props.updateStatusThunk}/>
            <div><b>contacts:</b></div>
            {Object.keys(props.profile.contacts).map(key => {
                    return <Contact contactTitle={key} contactValue={props.profile.contacts[key]}/>
                }
            )}

        </div>
    )
}



const Contact = ({contactTitle, contactValue}) => {
    return (
        <div>
            <b>{contactTitle}</b> {contactValue}
        </div>
    )
}

export default ProfileContent;
