import React, { useState, useEffect } from "react";

const StatusProfileHook = (props) => {

    let [editMode, setEditMode] = useState(false);
    let [status, setStatus] = useState(props.status);

    useEffect(() => {
        setStatus(props.status);
    }, [props.status]);



    let activateEditMode = () => {
        setEditMode(true)
    }

    let DeactivateEditMode = () => {
        setEditMode(false)
        props.updateStatusThunk(status);
    }

    let updateStatus = (e) => {
        setStatus(e.currentTarget.value);

    }






    return (
        <>
            {!editMode &&
                <div onClick={activateEditMode}> <b>Статус:</b> {props.status || "Введите статус"}</div>}

            {editMode &&
                <div>
                    <input onChange={updateStatus} autoFocus="true" value={status} />
                    <button onClick={DeactivateEditMode} >Применить</button>
                </div>
            }

        </>
    )

}

export default StatusProfileHook;
