import React from "react";
import s from "./Profile.module.scss";
import MyPostsContainer from "./MyPosts/MyPostsContainer";
import ProfileContent from "./PersonContent/ProfileContent";

function Profile(props) {

  return (
    <div>
      <img src="title-img.png" className={s.titleimg} alt="" />

      <ProfileContent goToEditProfile={props.goToEditProfile}  editProfile={props.editProfile} setEditProfile={props.setEditProfile} savePhoto={props.savePhoto} isUserUndef={props.isUserUndef}
                      profile={props.profile} status={props.status} updateStatusThunk={props.updateStatusThunk} />
      <MyPostsContainer />
    </div >
  );
}

export default Profile;
