import React from "react";
import { connect } from "react-redux";
import Profile from './Profile';
import { getProfileThunk, getStatusThunk, updateStatusThunk, savePhoto, setEditProfile, goToEditProfile } from '../../Redux/profile-reducer';
import { withRouter } from "react-router-dom";
import { withAuthRedicert } from "../../hoc/withAuthRedirect";
import { compose } from "redux";
import { getEditProfile, getProfile, getStatus, getUserId } from "../../Redux/profile-select";

class ProfileContainer extends React.Component {

  refreshUserId() {
    let userId = this.props.match.params.userId;
    if (!userId) {
      userId = this.props.userId;
    }
    this.props.getProfileThunk(userId);
    this.props.getStatusThunk(userId);
  }

  componentDidMount() {
    this.refreshUserId()
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    if (this.props.match.params.userId != prevProps.match.params.userId) this.refreshUserId();

  }


  render() {
    return (
      <div>
        <Profile goToEditProfile={this.goToEditProfile} editProfile={this.editProfile} setEditProfile={this.setEditProfile} savePhoto={this.savePhoto} isUserUndef={!!this.props.match.params.userId} {...this.props} props={this.props.profile} isAuth={this.props.isAuth} status={this.props.status} updateStatusThunk={this.props.updateStatusThunk} />
      </div>
    )
  }
}




let mapStateToProps = (state) => ({
  profile: getProfile(state),

  status: getStatus(state),
  userId: getUserId(state),
  editProfile: getEditProfile(state)
});

export default compose(
  connect(mapStateToProps, { getProfileThunk, getStatusThunk, updateStatusThunk, savePhoto, setEditProfile, goToEditProfile }),
  withRouter,
  withAuthRedicert
)(ProfileContainer)