import React from "react";
import s from "./MyPosts.module.scss";
import Post from "./Post/Post";
import { Field, reduxForm } from "redux-form";
import { required, maxLengthCreator } from "../../../util/validate";
import { TextArea } from "../../common/formsControls";

let maxLength = maxLengthCreator(10);

const MyPosts = React.memo((props) => {

  let postsElement = props.posts.map(p => <Post key={p.id} message={p.message} />);

  let onAddPost = (value) => {
    props.addPost(value.postText);
  }
  return (
    < div className={s.message_content} >
      <h3>My Post</h3>
      <AddNewPostRedux onSubmit={onAddPost} />
      <div className={s.post_content}>
        {postsElement}
      </div>
    </div >
  );

});

const AddNewPost = (props) => {
  return (
    <form onSubmit={props.handleSubmit}>
      <Field name="postText" component={TextArea} placeholder="Введите сообщение"
        validate={[required, maxLength]}></Field>
      <button >Sent post</button>
    </form>

  );
}

const AddNewPostRedux = reduxForm({ form: "newPostProfile" })(AddNewPost);

export default MyPosts;
